* {

    font-family: "Source Sans Pro", serif;
    font-style: normal;
    font-variant-caps: normal;
    font-variant-ligatures: normal;
    font-variant-numeric: normal;
    font-variant-east-asian: normal;
    font-weight: 300;
    line-height: 130%;
}

.view-container {
    width: 40rem;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /*padding: 1rem;*/
}

h1 {

}

h2 {

}

h3 {

}

.content-container {
    /*width: 35rem;*/
    width: 100%;
    margin: 0 0 2rem 0;
    box-shadow: 2px 8px 12px rgba(0, 0, 0, 0.3);
    padding: 2rem;
    border-radius: 8px;

}

.clickable-content-container {
    /*width: 35rem;*/
    width: 100%;
    margin: 0 0 2rem 0;
    box-shadow: 2px 8px 12px rgba(0, 0, 0, 0.3);
    padding: 2rem;
    border-radius: 8px;

    transition: background-color 0.2s ease;
    cursor: pointer;
}

.clickable-content-container:hover {
    background-color: #dcdcdc;
}

.text-form {
    width: 50%;
    padding: 0.3rem 0.3rem 0.3rem 0.3rem;
    border-width: thin;
    border-radius: 6px;
}

.large-text-form {
    padding: 0.3rem 0.3rem 0.3rem 0.3rem;
    border-width: thin;
    width: 90%;
    border-radius: 6px;
}

.button-list {
    width: 100%;
    box-sizing: border-box;
    padding: 2rem 0 0 0;
}

.button {
    width: 100%;
    height: 2.5rem;
    margin: 0 0 1rem 0;
}

.menu-button-list {
    width: 50%;
    box-sizing: border-box;
    padding: 3rem 0 3rem 0;
}

.accent-button {
    width: 100%;
    height: 2.5rem;
    margin: 0 0 1rem 0;
    color: white;
    background-color: #e30613;
    border-color: #e30613;
}

.button:hover {
    color: black !important;
    background-color: #d9d9d9 !important;
    border-color: #b8b8b8 !important;
}
.accent-button:hover {
    color: white !important;
    background-color: #a0000f !important;
    border-color: #a0000f !important;
}

 .time-picker {
     width: 7rem;
 }

.img {
    width: 100%;
    border-radius: 6px;
}

.logo-img {
    width: 400px;
    padding: 2rem 2rem 0 2rem
}

.language_menu {
    padding: 2rem;
}

.language_flag_img {
    padding: 0.2rem;
    width: 40px;
    height: 20px;

    cursor: pointer;
}


.form-field {
    margin-bottom: 1rem;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    margin-bottom: 0.5rem;
}

.calendar-container {
    /*max-width: 35rem;*/
    padding: 0 0 2rem 0;
    background-color: #fff;
    color: #222;
    border-radius: 8px;
}

.react-calendar {
    width: 100% !important;
    padding: 1rem 1rem 1rem 1rem;
    background-color: #fff !important;
    color: #222 !important;
    border-radius: 8px !important;
    font-family: "Source Sans Pro", serif !important;
    line-height: 1.125em !important;
}

.react-calendar__month-view__weekdays__weekday abbr {
    text-decoration: none !important;
}

.react-calendar__navigation button {
    color: #e30613 !important;
    min-width: 44px !important;
    background: none !important;
    font-size: 16px !important;
    margin-top: 8px !important;
    font-family: "Source Sans Pro", serif !important;

}

.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
    /*background-color: #f8f8fa !important;*/
    background-color: #d5353f !important;
    font-family: "Source Sans Pro", serif !important;

}

.react-calendar__navigation button[disabled] {
    background-color: #f0f0f0 !important;
    font-family: "Source Sans Pro", serif !important;

}

.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
    /*background-color: #f8f8fa !important;*/
    background-color: #e0e0e0 !important;
    color: #e30613 !important;
    border-radius: 6px !important;
    font-family: "Source Sans Pro", serif !important;

}

.react-calendar__tile--now {
    font-family: "Source Sans Pro", serif !important;

    background: #e3061333 !important;
    border-radius: 6px !important;
    font-weight: bold !important;
    color: #e30613 !important;
}

.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
    background: #e3061333 !important;
    border-radius: 6px !important;
    font-weight: bold !important;
    font-family: "Source Sans Pro", serif !important;

    color: #e30613 !important;
}

.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
    /*background-color: #f8f8fa !important;*/
    background-color: #e0e0e0 !important;
    font-family: "Source Sans Pro", serif !important;

}

.react-calendar__tile--active {
    background: #e30613 !important;
    border-radius: 6px !important;
    font-weight: bold !important;
    font-family: "Source Sans Pro", serif !important;

    color: white !important;
}

.react-calendar__tile--active:enabled:focus {
    /*background: #e30613 !important;*/
    /*background: rgb(255, 255, 255) !important;*/
    background-color: #e0e0e0 !important;

    font-family: "Source Sans Pro", serif !important;

    /*color: white !important;*/
    color: #222 !important;
}

.react-calendar--selectRange .react-calendar__tile--hover {
    /*background-color: #f8f8fa !important;*/
    background-color: #e0e0e0 !important;
    font-family: "Source Sans Pro", serif !important;

}

.react-calendar__tile--range {
    border-radius: 0 !important;
    background: #e30613 !important;
    color: #ffffff !important;
    font-family: "Source Sans Pro", serif !important;

}

.react-calendar__tile--rangeStart {
    border-radius: 6px 0 0 6px !important;
    background: #e30613 !important;
    color: #ffffff !important;
    font-family: "Source Sans Pro", serif !important;

}

.react-calendar__tile--rangeEnd {
    border-radius: 0 6px 6px 0 !important;
    background: #e30613 !important;
    color: #ffffff !important;
    font-family: "Source Sans Pro", serif !important;

}

label {
    display: block;
    font-size: 1rem;
    margin-bottom: 0.5rem;
}

/* Form select style */
select {
    width: 100%;
    padding: 0.5rem;
    font-size: 1rem;
    border-radius: 8px;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2); /* Add the box shadow */
}

option[value=""] {
    color: gray; /* Set the default option text color to gray */
}
